import { TimesheetController } from "@/controllers/project/timesheet.controller";
import { TimesheetDTO } from "@/dots/project/timesheet.dto";
import { authorizationMiddleware } from "@/middlewares/authorizationMiddleware";
import { ValidationMiddleware } from "@/middlewares/ValidationMiddleware";
import { Router } from "express";

export class TimesheetRoute {
    public path = "/timesheets";
    public router = Router();
    private timesheetController = new TimesheetController();

    constructor() {
        this.initializeRoutes();
    }

    private initializeRoutes() {
     
        this.router.post(`${this.path}/createTimeSheet`,authorizationMiddleware,ValidationMiddleware(TimesheetDTO), this.timesheetController.createTimesheet);
        this.router.put(`${this.path}/updateTimeSheet`,authorizationMiddleware,ValidationMiddleware(TimesheetDTO), this.timesheetController.updateTimesheet);
        this.router.get(`${this.path}/getUserTimeSheet`,authorizationMiddleware, this.timesheetController.getTimesheetsByUser);
        this.router.get(`${this.path}/project/:projectId`,authorizationMiddleware, this.timesheetController.getTimesheetsByProject);
        this.router.get(`${this.path}`,authorizationMiddleware, this.timesheetController.getAllTimesheets);
        this.router.delete(`${this.path}/:timesheetId`, authorizationMiddleware,this.timesheetController.deleteTimesheet);
    

        this.router.get(`${this.path}/getTimesheetById/:id`,authorizationMiddleware, this.timesheetController.getTimesheetById);
        this.router.get(`${this.path}/date-range`,authorizationMiddleware, this.timesheetController.getTimesheetsByDateRange);
        this.router.get(`${this.path}/user/:userId/project/:projectId`,authorizationMiddleware, this.timesheetController.getTimesheetsByUserAndProject);
        this.router.get(`${this.path}/user/:userId/total-hours`,authorizationMiddleware, this.timesheetController.getTotalHoursByUser);
        this.router.get(`${this.path}/summary`,authorizationMiddleware, this.timesheetController.getTimesheetSummary);
        this.router.patch(`${this.path}/:id/approve`,authorizationMiddleware, this.timesheetController.approveTimesheet);
        this.router.patch(`${this.path}/:id/reject`, authorizationMiddleware,this.timesheetController.rejectTimesheet);
    }
}
